* {
  box-sizing: border-box;
  padding: 10px;
  margin: 0;
}


html {
  height: 100%;
}

body {
  min-height: 100%;
  line-height: 1;
  font-family: sans-serif;
}  

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
}

input, select, option, optgroup, textarea, button, pre, code {
  font-size: 100%;
  font-family: inherit;
}

ol, ul {
  list-style: none;
}


body {
  background-color: cadetblue;
}

.header {
  padding: 20px;
  font-family: "Trebuchet MS", sans-serif, Arial;
  background-color: darkseagreen;
  color: #000;
  display: flex;
  justify-content: flex-end;
}

.header nav {
  padding-top: 10px;
  margin-right: 20px;

  font-family: 'Gill Sans', sans-serif, Calibri;
  font-size: 20px;
}

.header nav ul {
  list-style-type: none;
}

.header nav ul li {
  display: inline-block;
  margin-left: 25px;
  cursor: pointer;
}

.header h1 {
  display: inline-block;
  font-size: 50px;
}


main {
  display: flex;
  flex: wrap;

}

.portfolio{
  display: flex;
  flex-wrap: wrap;
}
.portfolio > div{
  width: 33%;;
}
.work-nobots {
  display: flex;
  flex: wrap;
  margin: 2%;
  /* flex: 1 0 200px; */
  padding: 10px;
  border-style: solid;
  border-width: 1px;
}

.work-pleiades {
  flex: 1 0 200px;
  margin: 2%;
  padding: 10px;
  border-style: solid;
  border-width: 1px;
}

.work-beagz-cal {
  margin: 2%;
  flex: 1 0 200px;
  padding: 10px;
  border-style: solid;
  border-width: 1px;
}

.work-gem {
  margin: 2%;
  flex: 1 0 200px;
  padding: 10px;
  border-style: solid;
  border-width: 1px;
}

.work-campeones {
  margin: 2%;
  flex: 1 0 200px;
  padding: 10px;
  border-style: solid;
  border-width: 1px;
}


.portfolio {
  margin-left: 20px;
  padding: 20px;
  clear: both;
  
  width: 80%;
  height: 100%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 25px;
  color: aliceblue;
  display: inline-block;

}


.portfolio h2 {
  color: aliceblue;
  font-size: 30px;
  padding-top: 0;
  padding-bottom: 10px;
}

.work-horiseon h3 {
  color: aliceblue;
  font-size: 25px;
  padding-top: 20px;
  padding-bottom: 20px;
}

a {
  color: aliceblue;
  text-decoration: none;
}

p {
  font-size: 20px;
  padding-top: 10px;

}

.work-horiseon {
  float: left;
  margin-left: 25px;
}

.work-horiseon img {
  max-height: 500px;
  padding-bottom: 20px;
}

.work-pleiades {

  margin-left: 25px;
}

.work-pleiades img {
  max-height: 400px;
  max-width: 100%;
}

.work-beagz-cal {
  float: left;
  margin-left: 25px;
}

.work-beagz-cal img {
  max-width: 100%;

  max-height: 250px
}

.work-gem {
 
  margin-left: 25px;
}

.work-gem img {
  max-width: 500px;
  max-height: 200px;
}

.work-campeones {
  float: left;
  margin-left: 25px;
}

.work-campeones img {
  max-height: 250px;
  max-width: 300px;
}

.content {


  display: flex;

}

.aside-about h2 {
  margin-bottom: 10px;
  text-align: center;
  display: inline-block;
  font-size: 25px;
  color: black;
}


.aside-contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-right: 0;
  padding: 20px;
  clear: both;
  float: right;   
  width: 20%;
  height: 100%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: aquamarine;
}

.aside-contact-links {
  margin-bottom: 32px;
  color: black;
}

.aside-contact-links h2 {
  margin-bottom: 10px;
  text-align: center;
  display: inline-block;
  font-size: 25px;
  color: black;
}

.aside-contact-links img {
  display: block;
  margin: 10px auto;
  max-width: 150px;
}

.aside-contact-links a {
 color: black;
 text-decoration: none;
 font-size: 20px;
}

.footer {
  padding: 30px;
  clear: both;
  font-family: 'Trebuchet MS', 
  'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  text-align: center;
  font-size: 25px;
}

form{
  max-width: 500px;
  display: flex;
  flex-direction: column;
}

